import React, { FC } from 'react';
import { ListGroup, ListGroupItem } from 'reactstrap';
import { ACTION_EVENTS, alertManager, AlertType, InputGroupInline, InputProps, InputType } from 'summer';
import { CLASS_NAME } from '../../../../models/dtos/mfp/eventoExample';
import { BasicEnum } from '../../../../models/enumerated/basicEnum';

export interface EventoInputSearchProps {
  naturezasValidas?: BasicEnum[];
}

const EventoIS: FC<InputProps & EventoInputSearchProps> = props => {
  const onChange = value => {
    if (props.onChange) {
      props.onChange(value);
    }
    if (value && props.naturezasValidas && props.naturezasValidas.length > 0) {
      const item = props.naturezasValidas.find(natureza => natureza.value === value.naturezaEvento);
      if (item === undefined) {
        props.formProps.setValue(props.id, null);
        alertManager.emit(ACTION_EVENTS.SHOW, {
          message: (
            <>
              <p>Apenas eventos com as seguintes naturezas são permitidas:</p>
              <ListGroup variant="flush">
                {props.naturezasValidas.map(natureza => (
                  <ListGroupItem key={natureza.value}>{natureza.label}</ListGroupItem>
                ))}
              </ListGroup>
            </>
          ),
          type: AlertType.ERROR,
        });
      }
    }
  };

  return (
    <InputGroupInline
      {...props}
      type={InputType.INPUT_SEARCH}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          {
            dataField: 'codigo',
            text: 'Código',
          },
          {
            dataField: 'descricao',
            text: 'Descrição',
          },
        ],
        dtoClassName: CLASS_NAME,
        searchViewId: 'EventoSearchView',
      }}
      onChange={onChange}
    />
  );
};

export default EventoIS;
