import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { Col, Row } from 'reactstrap';
import {
  AlertType,
  Box,
  CALIMA_DANGER,
  Checkbox,
  compare,
  DetailEditForm,
  DetailEditViewFormProps,
  formatDate,
  InputGroupInline,
  InputType,
  isEmpty,
  parseLastDay,
  parseRefToDate,
} from 'summer';
import { VinculoEvento } from '../../../../models/dtos/mfp/vinculoEvento';
import { EMPREGADO_CONTRATO_VERDE_AMARELO_COM_ANT_FGTS } from '../../../../models/enumerated/mfp/categoriaEnum';
import { EVENTO_FIXO_1_PARC_13_ENUM_VALUES, EVENTO_FIXO_ADIANTAMENTO_ENUM_VALUES } from '../../../../models/enumerated/mfp/eventoFixoEnum';
import {
  ADIANTAMENTO,
  DECIMO_TERCEIRO_PARCELA_1,
  RECISAO,
  RECISAO_COMPLEMENTAR,
  RESILICAO,
  RESILICAO_COMPLEMENTAR,
  TIPO_CALCULO_ENUM_VALUES,
} from '../../../../models/enumerated/mfp/tipoCalculoEnum';
import { EVENTOS_INFORMATIVOS_VALUES, TIPO_EVENTO_ENUM_VALUES } from '../../../../models/enumerated/mfp/tipoEventoEnum';
import { NORMAL } from '../../../../models/enumerated/mfp/vinculoEventoOrigemEnum';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';
import EnumUtil from '../../../../utilities/enum.util';
import { promisedAlert } from '../../../../utilities/vinculo.util';
import EventoIS from '../../../components/mfp/eventoIS/EventoIS';

interface ScreenFields {
  eventoFormula?: string;
  eventoTipo?: string;
  referenciaMFP?: string;
  somenteEstaReferencia?: boolean;
}

const VinculoEventoDetailEditView: FC<DetailEditViewFormProps<VinculoEvento>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const formProps = useForm<VinculoEvento & ScreenFields>({ defaultValues: { origem: NORMAL.value, ...(props.selectedItem || {}) }, mode: 'onBlur' });

  const [modoTela, setModoTela] = useState('VALOR');
  const [valorVariavelDisabled, setValorVariavelDisabled] = useState(true);
  const [somenteEstaReferencia, setSomenteEstaReferencia] = useState(true);
  const [isEventoInformativo, setIsEventoInformativo] = useState(false);

  useEffect(
    () => {
      formProps.register({ name: 'origem' });
      formProps.register({ name: 'vinculo' });

      const ref = globalReducer.globalParameter.empresaMFP.referencia;
      formProps.setValue('referenciaMFP', ref);

      if (props.selectedItem && !isEmpty(props.selectedItem.id)) {
        setSomenteEstaReferencia(false);
        eventoChange(props.selectedItem.evento);
      } else {
        const parentData = props.parentFormProps.getValues();
        formProps.setValue([
          { vinculo: parentData.vinculo },
          { tipoCalculo: parentData.tipoCalculo },
          { dataInicial: formatDate(parseRefToDate(ref, 1)) },
          { dataFinal: formatDate(parseLastDay(ref)) },
          { somenteEstaReferencia: true },
        ]);
      }
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const getScreenMode = formula => {
    let result = null;

    const contains = key => formula.toLowerCase().indexOf(key) !== -1;

    if (contains('<horas>')) {
      result = 'HORAS';
    } else if (contains('<dias>')) {
      result = 'DIAS';
    } else if (contains('<valor>')) {
      result = 'VALOR';
    }

    return result;
  };

  const desabilitarEventoInformativo = tipo => {
    const eventoInformativo = EnumUtil.contains(EVENTOS_INFORMATIVOS_VALUES, tipo);
    setIsEventoInformativo(eventoInformativo);
    formProps.setValue('principal', !eventoInformativo);
  };

  const eventoChange = value => {
    if (value && value.formula) {
      formProps.setValue([{ eventoFormula: value.formula }, { eventoTipo: EnumUtil.find(TIPO_EVENTO_ENUM_VALUES, value.tipo).label }]);
      setModoTela(getScreenMode(value.formula) || 'VALOR');
      valorManualChange();
      desabilitarEventoInformativo(value.tipo);
    }
  };

  const valorManualChange = () => {
    const values = formProps.getValues();
    const disabled = !values.valorManual && (!values.evento || !getScreenMode(values.evento.formula));
    setValorVariavelDisabled(disabled);
  };

  const somenteEstaReferenciaChange = () => {
    const values = formProps.getValues();
    const ref = globalReducer.globalParameter.empresaMFP.referencia;

    setSomenteEstaReferencia(values.somenteEstaReferencia);

    formProps.setValue([
      { dataInicial: values.somenteEstaReferencia ? formatDate(parseRefToDate(ref, 1)) : null },
      { dataFinal: values.somenteEstaReferencia ? formatDate(parseLastDay(ref)) : null },
    ]);
  };

  const validateExtras = (formData, _, parentData) => {
    const erros = [];
    let pass = true;

    if (isEmpty(formData.dataInicial)) {
      pass = false;
    }

    if (pass) {
      if (!isEmpty(formData.dataFinal) && compare(formData.dataFinal, formData.dataInicial) < 0) {
        erros.push('A Dt. Inicial não pode ser maior do que a Dt. Final.');
      }

      const entreFerias =
        parentData.vinculo.ferias.filter(
          it => compare(it.dtInicialGozo, formData.dataInicial) <= 0 && compare(it.dtFinalGozo, formData.dataInicial) >= 0
        ).length > 0;

      if (entreFerias) {
        promisedAlert('Trabalhador em período de Férias.', AlertType.WARNING);
      }

      if (formData.tipoCalculo && !isEmpty(formData.evento)) {
        if (
          formData.tipoCalculo === ADIANTAMENTO.value &&
          !EnumUtil.contains(EVENTO_FIXO_ADIANTAMENTO_ENUM_VALUES, formData.evento.codigo, 'intValue')
        ) {
          erros.push(
            'Somente os seguintes eventos são permitídos no cálculo do tipo Adiantamento:\n' +
              EVENTO_FIXO_ADIANTAMENTO_ENUM_VALUES.map(it => it.label).join('\n')
          );
        }

        if (
          formData.tipoCalculo === DECIMO_TERCEIRO_PARCELA_1.value &&
          !EnumUtil.contains(EVENTO_FIXO_1_PARC_13_ENUM_VALUES, formData.evento.codigo, 'intValue')
        ) {
          erros.push(
            'Somente os seguintes eventos são permitídos na 1ª Parcela do 13º:\n' + EVENTO_FIXO_1_PARC_13_ENUM_VALUES.map(it => it.label).join('\n')
          );
        }

        if (
          EnumUtil.contains([RECISAO, RECISAO_COMPLEMENTAR, RESILICAO_COMPLEMENTAR, RESILICAO], formData.tipoCalculo) &&
          formData.evento.insideSalarioComposto
        ) {
          erros.push('Eventos que incidem salário composto não pode ser lançados na rescisão pois já integram parte do saldo salário');
        }
      }

      if (parentData.vinculo.categoria === EMPREGADO_CONTRATO_VERDE_AMARELO_COM_ANT_FGTS.value && formData.evento.codigo === 299) {
        erros.push(
          'O lançamento do evento 299 - Indenização antecipada sobre o saldo FGTS (Multa 20% - Contrato Verde Amarelo), só é permitido ' +
            'para a categoria 108 - Empregado - Contrato de trabalho Verde e Amarelo - com acordo para antecipação mensal da multa ' +
            'rescisória do FGTS'
        );
      }
    }

    return erros;
  };

  const handleSubmit = (formData, validateExtrasFunc) => {
    const vinculo = formProps.getValues().vinculo;

    if (compare(formData.dataInicial, vinculo.dataAdmissao) < 0) {
      formData.dataInicial = vinculo.dataAdmissao;

      if (compare(formData.dataFinal, formData.dataInicial) < 0) {
        formData.dataFinal = formData.dataInicial;
      }

      formProps.setValue([{ dataInicial: formData.dataInicial }, { dataFinal: formData.dataFinal }]);

      promisedAlert('A Data Inicial foi alterada para a Data de Admissão. Deseja gravar?').then(() => {
        props.handleSubmit(formData, validateExtrasFunc);
      });
    } else {
      props.handleSubmit(formData, validateExtrasFunc);
    }
  };

  return (
    <DetailEditForm handleSubmit={handleSubmit} handleCancel={props.handleCancel} formProps={formProps} validateExtras={validateExtras}>
      {isEventoInformativo && (
        <Row>
          <Col style={{ color: CALIMA_DANGER, textAlign: 'center' }}>
            <h4>ATENÇÃO</h4>
            <p>Os eventos informativos não são exibidos no contra cheque.</p>
          </Col>
        </Row>
      )}
      <Row>
        <Col>
          <InputGroupInline
            id="tipoCalculo"
            label="Tipo de Cálculo"
            formProps={formProps}
            isPrependLabel={true}
            inputDropDownProps={{ optionsProvider: TIPO_CALCULO_ENUM_VALUES }}
            type={InputType.DROP_DOWN}
            placeholder="Selecione o Tipo de Cálculo"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <EventoIS
            id="evento"
            formProps={{ ...formProps, validation: { required: true } }}
            label="Evento"
            isPrependLabel={true}
            onChange={eventoChange}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline id="eventoFormula" label="Fórmula" formProps={formProps} isPrependLabel={true} disabled={true} />
        </Col>
        <Col>
          <InputGroupInline id="eventoTipo" label="Tipo" formProps={formProps} isPrependLabel={true} disabled={true} />
        </Col>
      </Row>
      <Row>
        <Col>
          <Box title="Periodicidade">
            <Row>
              <Col>
                <InputGroupInline id="referenciaMFP" label="Referência atual" formProps={formProps} isPrependLabel={true} disabled={true} />
              </Col>
            </Row>
            <div className="d-flex">
              <div style={{ width: 325, marginTop: 10 }}>
                <Checkbox id="somenteEstaReferencia" formProps={formProps} label="Somente esta referência" onChange={somenteEstaReferenciaChange} />
              </div>
              <div className="flex-grow-1" style={{ marginRight: 20 }}>
                <InputGroupInline
                  id="dataInicial"
                  label="Dt. Inicial"
                  formProps={{ ...formProps, validation: { required: true } }}
                  isPrependLabel={true}
                  type={InputType.DATE}
                  disabled={somenteEstaReferencia}
                />
              </div>
              <div className="flex-grow-1">
                <InputGroupInline
                  id="dataFinal"
                  label="Dt. Final"
                  formProps={formProps}
                  isPrependLabel={true}
                  type={InputType.DATE}
                  disabled={somenteEstaReferencia}
                />
              </div>
            </div>
          </Box>
        </Col>
      </Row>
      <div className="d-flex">
        <div style={{ width: 350 }}>
          {(modoTela === 'VALOR' || modoTela === 'DIAS') && (
            <InputGroupInline
              id="variavel"
              label={'Valor' + (modoTela === 'DIAS' ? ' Dias' : '')}
              formProps={{ ...formProps, validation: { required: !valorVariavelDisabled } }}
              isPrependLabel={true}
              disabled={valorVariavelDisabled}
            />
          )}
          {modoTela === 'HORAS' && (
            <InputGroupInline
              id="variavel"
              label="Valor"
              formProps={{ ...formProps, validation: { required: !valorVariavelDisabled } }}
              isPrependLabel={true}
              type={InputType.FRACTION_OF_TIME}
              disabled={valorVariavelDisabled}
            />
          )}
        </div>
        <div style={{ marginLeft: 20, marginTop: 10, width: 150 }}>
          <Checkbox id="valorManual" formProps={formProps} label="Valor Manual" onChange={valorManualChange} />
        </div>
        <div style={{ marginLeft: 20, marginTop: 10, width: 230 }}>
          <Checkbox id="principal" formProps={formProps} label="Exibir no contra cheque" />
        </div>
      </div>
    </DetailEditForm>
  );
};

export default VinculoEventoDetailEditView;
