import React, { FC, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useDispatch, useSelector } from 'react-redux';
import { RouteComponentProps, useHistory } from 'react-router';
import { Col, Row } from 'reactstrap';
import {
  boolColumn,
  Box,
  compare,
  decimalFormatter,
  DefaultSortProps,
  DetailManagerForm,
  DetailManagerFormProps,
  EditViewProps,
  enumColumn,
  findAllByExpression,
  findById,
  formatDate,
  getSelectedItem,
  InputGroupInline,
  InputType,
  isEmpty,
  OrderType,
  parseRefToDate,
  persist,
  ProcessBar,
  progressActions,
  refToFormat,
  WindowComponent,
  Wizard,
  WizardActionFinishProps,
  WizardPage,
} from 'summer';
import { CLASS_NAME as CLASS_VINCULO, Vinculo } from '../../../../models/dtos/mfp/vinculo';
import { CLASS_NAME, VinculoEvento } from '../../../../models/dtos/mfp/vinculoEvento';
import {
  APRESENTACAO_VINCULO_EVENTO_ENUM_VALUES,
  ATIVOS,
  FUTUROS,
  TODOS,
  VENCIDOS,
} from '../../../../models/enumerated/mfp/apresentacaoVinculoEventoEnum';
import { FOLHA, TIPO_CALCULO_ENUM_VALUES } from '../../../../models/enumerated/mfp/tipoCalculoEnum';
import { TIPO_EVENTO_ENUM_VALUES } from '../../../../models/enumerated/mfp/tipoEventoEnum';
import { validarEventoVinculo } from '../../../../services/mfp.service';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';
import EnumUtil from '../../../../utilities/enum.util';
import {
  getItemAtual,
  getSalarioReferencia,
  numHorasFormatter,
  promisedAlert,
  showLoading,
  valorTotalOutrasRemuneracoes,
} from '../../../../utilities/vinculo.util';
import VinculoIS from '../../../components/mfp/vinculoIS/VinculoIS';
import VinculoEventoDetailEditView from './VinculoEventoDetailEditView';

interface VinculoEventoEditViewProps extends EditViewProps<VinculoEvento> {
  onDockSubmit?: (data: any) => void;
  openViaDock?: boolean;
}

interface VinculoExtraFields {
  label?: string;
}

interface VinculoEventoFormProps {
  vinculo?: Vinculo & VinculoExtraFields;
  eventos?: VinculoEvento[];
  eventosFiltrados?: VinculoEvento[];
  tipoCalculo?: string;
  apresentacao?: string;
  nomeTrabalhador?: string;
  dataAdmissao?: string;
  dataAfastamento?: string;
  dataRetorno?: string;
  motivoAfastamento?: string;
  categoria?: string;
  valeTransporte?: string;
  nroDependentesSalarioFamilia?: string;
  nroDependentesIRPF?: string;
  salarioValor?: string;
  numeroHorasContratuais?: string;
  valorTotalOutrasRemuneracoes?: string;
}

const VinculoEventoEditView: FC<VinculoEventoEditViewProps & RouteComponentProps> = props => {
  const history = useHistory();
  const selectedItem = getSelectedItem(props);
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const formProps = useForm<VinculoEventoFormProps>({
    defaultValues: {
      apresentacao: ATIVOS.value,
      eventos: [],
      eventosFiltrados: [],
      tipoCalculo: FOLHA.value,
    },
    mode: 'onBlur',
  });
  const dispatch = useDispatch();

  useEffect(
    () => {
      setTimeout(() => {
        formProps.register({ name: 'eventos' });
        if (selectedItem && selectedItem.id) {
          formProps.register({ name: 'vinculo' });
          loadVinculo(selectedItem.id);
        }
      }, 700);
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const onPageChange = currentPageId => {
    if (currentPageId === 'eventos') {
      loadVinculo();
    }
  };

  const loadVinculo = (vinculoId = null) => {
    const id = vinculoId || formProps.getValues().vinculo.id;
    dispatch(showLoading());
    findById({ id }, CLASS_VINCULO, {
      thenFunction: res => {
        formProps.setValue('vinculo', res);
        loadEventos(id);
      },
    });
  };

  const loadEventos = (vinculoId, tipoCalculo = FOLHA.value, apresentacao = ATIVOS.value) => {
    findAllByExpression(
      {
        className: CLASS_NAME,
        expressions: [
          {
            fieldName: 'vinculo.id',
            value: vinculoId,
          },
        ],
        fetchType: 'ALL',
        max: 1000,
        start: 0,
      },
      {
        thenFunction: res => {
          dispatch(progressActions.hideProgress());
          formProps.setValue('eventos', res.content);
          setReadOnlyFields();
          filtrarEventos(res.content, tipoCalculo, apresentacao);
        },
      }
    );
  };

  const filtroApresentacao = (apresentacao, evento, dataAtual) => {
    const validar = {
      [ATIVOS.value]: () => {
        const checkA = compare(evento.dataInicial, dataAtual) <= 0 && compare(evento.dataFinal, dataAtual) >= 0;
        const checkB = compare(evento.dataInicial, dataAtual) <= 0 && isEmpty(evento.dataFinal);
        return checkA || checkB;
      },
      [FUTUROS.value]: () => compare(evento.dataInicial, dataAtual) > 0,
      [VENCIDOS.value]: () => compare(evento.dataFinal, dataAtual) < 0,
      [TODOS.value]: () => true,
    };

    return validar[apresentacao]() || false;
  };

  const filtrarEventos = (eventos, tipo, apresentacao) => {
    const dataAtual = formatDate(parseRefToDate(globalReducer.globalParameter.empresaMFP.referencia, 0));
    formProps.setValue(
      'eventosFiltrados',
      eventos.filter(evento => evento.tipoCalculo === tipo && filtroApresentacao(apresentacao, evento, dataAtual))
    );
  };

  const filtrarEventosTipo = value => {
    const values = formProps.getValues();
    filtrarEventos(values.eventos, value, values.apresentacao);
  };

  const filtrarEventosApresentacao = value => {
    const values = formProps.getValues();
    filtrarEventos(values.eventos, values.tipoCalculo, value);
  };

  const setReadOnlyFields = () => {
    const values = formProps.getValues();
    const ultimoAfastamento = getItemAtual(values.vinculo.movimentacoes);
    const salarioAtual = getSalarioReferencia(values.vinculo, globalReducer.globalParameter.empresaMFP.referencia);

    const nroDependentesSalarioFamilia = values.vinculo.trabalhador.dependentes.filter(item => item.dependeteSalarioFamilia).length;
    const nroDependentesIRPF = values.vinculo.trabalhador.dependentes.filter(item => item.dependenteIRPJ).length;

    formProps.setValue([
      { nomeTrabalhador: values.vinculo.label },
      { dataAdmissao: values.vinculo.dataAdmissao },
      { dataAfastamento: ultimoAfastamento && ultimoAfastamento.dataAfastamento },
      { dataRetorno: ultimoAfastamento && ultimoAfastamento.dataRetorno },
      { motivoAfastamento: ultimoAfastamento && ultimoAfastamento.motivoAfastamento },
      { categoria: values.vinculo.cbo.descricao },
      { valeTransporte: `Existe(m) ${values.vinculo.valesTransporte.length} vale(s) cadastrado(s)` },
      { nroDependentesSalarioFamilia },
      { nroDependentesIRPF },
      { salarioValor: decimalFormatter(salarioAtual.valor, null) },
      { numeroHorasContratuais: numHorasFormatter(salarioAtual.numeroHorasContratuais) },
      { valorTotalOutrasRemuneracoes: valorTotalOutrasRemuneracoes(salarioAtual.outrasRemuneracoes) },
    ]);
  };

  const onBefore = edit => (itemData, done) => {
    if (!isEmpty(itemData.id)) {
      dispatch(showLoading());
      validarEventoVinculo(itemData, {
        thenFunction: res => {
          dispatch(progressActions.hideProgress());
          if (res) {
            const ref = refToFormat(globalReducer.globalParameter.empresaMFP.referencia);
            const tipoCalculo = EnumUtil.find(TIPO_CALCULO_ENUM_VALUES, itemData.tipoCalculo).label;
            const event = edit ? 'alteração' : 'remoção';
            promisedAlert(
              `Esse evento já foi calculado. Para a ${event} ter efeito é necessário refazer os cálculos ` +
                `de ${tipoCalculo} a partir de ${ref}. Deseja continuar?`
            ).then(() => done());
          } else {
            done();
          }
        },
      });
    } else {
      done();
    }
    return { async: true };
  };

  const onChange = (_, item) => {
    dispatch(showLoading());
    persist(item, CLASS_NAME, {
      thenFunction: () => {
        const values = formProps.getValues();
        loadEventos(values.vinculo.id, values.tipoCalculo, values.apresentacao);
      },
    });
  };

  const onCancel = () => {
    if (!props.openViaDock) {
      formProps.reset();
    }
    history.goBack();
  };

  const eventoDefaultSortProps: DefaultSortProps[] = [
    {
      dataField: 'dataInicial',
      order: OrderType.ASC,
    },
  ];

  const eventoFormProps: DetailManagerFormProps = {
    columns: [
      {
        dataField: 'evento.codigo',
        text: 'Código',
      },
      {
        dataField: 'evento.descricao',
        text: 'Descrição',
      },
      enumColumn(
        {
          dataField: 'evento.tipo',
          text: 'Tipo',
        },
        TIPO_EVENTO_ENUM_VALUES
      ),
      {
        dataField: 'variavel',
        text: 'Variável',
      },
      {
        dataField: 'dataInicial',
        text: 'Início',
      },
      {
        dataField: 'dataFinal',
        text: 'Fim',
      },
      boolColumn({
        dataField: 'valorManual',
        text: 'Valor Manual',
      }),
      {
        dataField: 'evento.formula',
        text: 'Fórmula',
      },
    ],
    detailView: VinculoEventoDetailEditView,
    detailViewSize: 'large',
    detailViewWikiId: 510,
    formProps,
    onBeforeEdit: onBefore(true),
    onBeforeRemove: onBefore(false),
    onChange,
    propName: 'eventosFiltrados',
    title: 'Lançamento de Eventos',
  };

  const wizardFinishProps: WizardActionFinishProps = {
    cancelButton: { label: 'Fechar' },
    confirmButton: null,
    onCancel,
  };

  const getEditView = () => (
    <>
      <Row>
        <Col>
          <InputGroupInline
            id="tipoCalculo"
            label="Tipo de Cálculo"
            formProps={formProps}
            isPrependLabel={true}
            inputDropDownProps={{ optionsProvider: TIPO_CALCULO_ENUM_VALUES, selectProps: { isClearable: false } }}
            type={InputType.DROP_DOWN}
            onChange={filtrarEventosTipo}
          />
        </Col>
        <Col>
          <InputGroupInline
            id="apresentacao"
            label="Apresentação dos Eventos"
            formProps={formProps}
            isPrependLabel={true}
            inputDropDownProps={{ optionsProvider: APRESENTACAO_VINCULO_EVENTO_ENUM_VALUES }}
            type={InputType.DROP_DOWN}
            onChange={filtrarEventosApresentacao}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <Box title="Trabalhador">
            <Row>
              <Col>
                <InputGroupInline id="nomeTrabalhador" formProps={formProps} disabled={true} label="Nome" isPrependLabel={true} />
              </Col>
            </Row>
            <Row>
              <Col>
                <InputGroupInline id="dataAdmissao" formProps={formProps} disabled={true} label="Dt. Admissão" isPrependLabel={true} />
              </Col>
              <Col>
                <InputGroupInline id="dataAfastamento" formProps={formProps} disabled={true} label="Dt. Afast" isPrependLabel={true} />
              </Col>
              <Col>
                <InputGroupInline id="dataRetorno" formProps={formProps} disabled={true} label="Dt. Ret" isPrependLabel={true} />
              </Col>
            </Row>
            <Row>
              <Col>
                <InputGroupInline id="motivoAfastamento" formProps={formProps} disabled={true} label="Motivo Afastamento" isPrependLabel={true} />
              </Col>
            </Row>
            <Row>
              <Col>
                <InputGroupInline id="categoria" formProps={formProps} disabled={true} label="Categoria" isPrependLabel={true} />
              </Col>
              <Col>
                <InputGroupInline id="valeTransporte" formProps={formProps} disabled={true} label="V. Transp." isPrependLabel={true} />
              </Col>
            </Row>
            <Row>
              <Col>
                <InputGroupInline id="nroDependentesSalarioFamilia" formProps={formProps} disabled={true} label="Dep. S. F." isPrependLabel={true} />
              </Col>
              <Col>
                <InputGroupInline id="nroDependentesIRPF" formProps={formProps} disabled={true} label="Dep. I. R." isPrependLabel={true} />
              </Col>
              <Col>
                <InputGroupInline id="salarioValor" formProps={formProps} disabled={true} label="Salário" isPrependLabel={true} />
              </Col>
            </Row>
            <Row>
              <Col>
                <InputGroupInline id="numeroHorasContratuais" formProps={formProps} disabled={true} label="Nº Horas Cont." isPrependLabel={true} />
              </Col>
              <Col>
                <InputGroupInline id="valorTotalOutrasRemuneracoes" formProps={formProps} disabled={true} label="Out. Rem." isPrependLabel={true} />
              </Col>
            </Row>
          </Box>
        </Col>
      </Row>
      <Row>
        <Col>
          <DetailManagerForm
            {...props}
            defaultSortDirection={OrderType.ASC}
            defaultSorted={eventoDefaultSortProps}
            {...eventoFormProps}
            keyField="rowIndex"
          />
        </Col>
      </Row>
    </>
  );

  formProps.watch('eventosFiltrados');

  return (
    <WindowComponent title="Manutenção de Eventos do Cargo do Trabalhador" wrapBody={props.openViaDock === true} wikiId={510}>
      {props.openViaDock === true ? (
        <div className="m-3">
          {getEditView()}
          <ProcessBar handleClose={onCancel} hideSubmitButton={true} btnCloseLabel="Fechar" />
        </div>
      ) : (
        <Wizard formProps={formProps} finishProps={wizardFinishProps} onAfterPageChange={onPageChange}>
          <WizardPage id="vinculo" title="Seleção do vínculo">
            <Row>
              <Col>
                <VinculoIS id="vinculo" formProps={{ ...formProps, validation: { required: true } }} label="Vinculo" isPrependLabel={true} />
              </Col>
            </Row>
          </WizardPage>
          <WizardPage id="eventos" title="Eventos">
            {getEditView()}
          </WizardPage>
        </Wizard>
      )}
    </WindowComponent>
  );
};

export default VinculoEventoEditView;
